.btn-score {
  padding-bottom: 25px;
}

.btn-score:hover {
  cursor: default;
}

#incorrect {
  -webkit-animation: bounce 0.82s linear infinite;
          animation: bounce 0.82s linear infinite;
}

@-webkit-keyframes bounce{
  0%, 20%, 50%, 80%, 100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
40% {
      -webkit-transform: translateY(-12px);
              transform: translateY(-12px);
  }
60% {
      -webkit-transform: translateY(-6px);
              transform: translateY(-6px);
  }
}

@keyframes bounce{
  0%, 20%, 50%, 80%, 100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
40% {
      -webkit-transform: translateY(-12px);
              transform: translateY(-12px);
  }
60% {
      -webkit-transform: translateY(-6px);
              transform: translateY(-6px);
  }
}

@media only screen and (max-width: 1350px) {
  .btn-score {
      margin-top: 0px;
      position: static;
      border-radius: 0px;
  }
}


